      width: 3rem;
      height: inherit;
      background-color: #1d355d;
      transition: all 0.5s; }
    .hamburger-menu .bar::before {
      top: -0.9rem; }
    .hamburger-menu .bar::after {
      top: 0.9rem; }

.open .bar {
  background-color: transparent; }
  .open .bar::before {
    top: 0;
    transform: rotate(135deg); }
  .open .bar::after {
    top: 0;
    transform: rotate(-135deg); }

